import setGeoData from './setGeoData';
import setWeather from './setWeather';
import setDate from './setDate';
import setBackground from './setBackground';
import changeDegrFormat from './changeDegrFormat';

const mapboxgl = require('mapbox-gl/dist/mapbox-gl.js');

const state = {
  timerId: '',
  timeZone: 'Europe/Minsk',
  location: 'Minsk, Belarus',
  geometry: '53.5359,27.3400',
  userCity: 'London',
  mapBox: [27.56, 53.90],
  language: 'EN',
  format: 'F',
};

mapboxgl.accessToken = 'pk.eyJ1IjoicmVoYXRlIiwiYSI6ImNqdXUzaXVtZzA4ZngzeXFlNjlmbjZ3N28ifQ.7D4pwodVuoUWC8ZoFIq-nA';
function updateMap() {
  document.querySelector('#map').innerHTML = '';
  const map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/mapbox/streets-v11',
    center: state.mapBox,
    zoom: 11,
  });
}

updateMap();
setBackground(state);

function setMapboxCoords() {
  const coords = state.geometry.split(',');
  coords.reverse();
  state.mapBox = coords;
}

function search(inp) {
  const input = document.querySelector('.header__input');
  let value = '';
  if (inp !== undefined) {
    value = inp;
  } else {
    value = input.value;
  }
  fetch(`https://api.opencagedata.com/geocode/v1/json?q=${value}&key=7c0433bb32b840ed96bb8271fc9eae75&language=en&pretty=1`)
    .then((resp) => resp.json())
    .then((data) => {
      if (state.timerId !== '') {
        clearInterval(state.timerId);
      }
      const result = data.results[0];
      state.geometry = `${result.geometry.lat.toFixed(4)},${result.geometry.lng.toFixed(4)}`;
      state.timeZone = `${result.annotations.timezone.name}`;
      state.location = `${result.formatted}`;
      setMapboxCoords();
      updateMap();
      setDate(state);
      const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
      const targetUrl = `https://api.darksky.net/forecast/e25a5a5c7e54a11fa122296b791abfbf/${state.geometry}?lang=ru`;
      return fetch(proxyUrl + targetUrl);
    })
    .then((resp) => resp.json())
    .then((data) => {
      setWeather(data, state);
      setGeoData(state);
    });
}


fetch('https://ipinfo.io/json?token=526aab9fffec80')
  .then((resp) => resp.json())
  .then((data) => {
    state.userCity = data.city;
    search(state.userCity);
  });

document.querySelector('.btn-search').addEventListener('click', () => { search(); });
document.querySelector('.btn-refresh').addEventListener('click', () => { setBackground(state); });
document.querySelector('.btn-degr-F').addEventListener('click', () => {
  changeDegrFormat('C');
  state.format = 'C';
  const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
  const targetUrl = `https://api.darksky.net/forecast/e25a5a5c7e54a11fa122296b791abfbf/${state.geometry}?lang=ru`;
  fetch(proxyUrl + targetUrl)
    .then((resp) => resp.json())
    .then((data) => {
      setWeather(data, state);
    });
});
document.querySelector('.btn-degr-C').addEventListener('click', () => {
  changeDegrFormat('F');
  state.format = 'F';
  const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
  const targetUrl = `https://api.darksky.net/forecast/e25a5a5c7e54a11fa122296b791abfbf/${state.geometry}?lang=ru`;
  fetch(proxyUrl + targetUrl)
    .then((resp) => resp.json())
    .then((data) => {
      setWeather(data, state);
    });
});
