const dateData = {
  days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
  months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'Septembe', 'October', 'November', 'December'],
};

const setDate = (state) => {
  const today = new Date();
  const dateArr = today.toLocaleString('ru-By', { timeZone: state.timeZone }).split(',');
  const date = dateArr[0].split('.');
  const dateStr = `${dateData.days[today.getDay()].slice(0, 3)} ${date[0]} ${dateData.months[date[1] - 1]} `;
  const time = `${today.getHours()}:${today.getMinutes()}`;
  state.timerId = setInterval(() => {
    const crntDate = new Date();
    const arr = crntDate.toLocaleString('ru-By', { timeZone: state.timeZone }).split(',')[1];
    const crntTime = `${arr.slice(0, -3)}`;
    document.querySelector('.weather__time').innerHTML = crntTime;
  }, 1000);
  document.querySelector('.weather__date').innerHTML = dateStr;
  document.querySelector('.weather__time').innerHTML = time;
  document.querySelectorAll('.weather__date--mini').forEach((el, ind) => {
    const node = el;
    const day = (today.getDay() + (ind + 1)) > 6
      ? (today.getDay() + (ind + 1) - 7) : (today.getDay() + (ind + 1));
    node.innerHTML = dateData.days[day];
  });
};

export default setDate;
