const changeDegrFormat = (format) => {
  if (format === 'C') {
    document.querySelector('.btn-degr-F').classList.add('header__button--active');
    document.querySelector('.btn-degr-C').classList.remove('header__button--active');
  } else {
    document.querySelector('.btn-degr-C').classList.add('header__button--active');
    document.querySelector('.btn-degr-F').classList.remove('header__button--active');
  }
};

export default changeDegrFormat;
