const setBackground = () => {
  let query = 'nature';
  const crntDate = new Date();
  const mounth = crntDate.getMonth() + 1;
  switch (mounth) {
    case (12 || 1 || 2):
      query += '+winter';
      break;
    case (3 || 4 || 5):
      query += '+spring';
      break;
    case (6 || 7 || 8):
      query += '+summer';
      break;
    default:
      query += '+autumn';
  }
  const hours = crntDate.getHours();
  switch (true) {
    case (hours > 6 && hours < 23):
      query += '+day';
      break;
    default:
      query += '+night';
  }
  const fetchBG = fetch(`https://api.unsplash.com/photos/random?orientation=landscape&per_page=1&query=${query}&client_id=2b41b908b8a26b21fb463e74d90981606ad3283389b6b8ad9082c0e2326ac146`);
  fetchBG
    .then((resp) => resp.json())
    .then((data) => {
      const img = new Image();
      img.src = data.urls.regular;
      img.onload = () => {
        document.querySelector('body').style.backgroundImage = `linear-gradient( rgba(94, 100, 117, 0.5) 100%, rgba(94, 100, 117, 0.5)100%), url(${img.src})`;
      };
    })
    .catch((err) => {
      const gradient = 'linear-gradient( rgba(94, 100, 117, 0.5) 100%, rgba(94, 100, 117, 0.5)100%)';
      document.querySelector('body').style.backgroundImage = `${gradient}, url("https://images.unsplash.com/photo-1548288973-797009d1e14d?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max&ixid=eyJhcHBfaWQiOjk4MDk5fQ")`;
    });
};

export default setBackground;
